  border-bottom: 4px solid #F3CD1A;
  color: black;
}

#athena-nav {
  padding-top: 10vh;
  padding-left: 15vw;
  padding-right: 15vw;
  background-color: #F3EAFD;
}

@font-face {
  font-family: 'Work Sans', sans-serif;
  src: url(/static/media/WorkSans-Regular.30be604d.ttf);
  src: url('https://fonts.googleapis.com/css?family=Work+Sans:400,700');
}

#hero-row {
  background-color: #F3EAFD;
  padding-left: 16vw;
  padding-right: 16vw;
  width: 100%;
