  display: inline-flex;
}

#about-h {
  margin-top: 20vh;
  margin-right: -2vw;
}

#athena-hero {
  margin-top: 10vh;
}

#underlined-h:after {
  content: url(/static/media/underline-header.2278658b.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -135px;
}

@media screen and (min-width: 1920px) {
  #about-h {
    margin-left: 20%;
    margin-right: 10px;
  }

  #athena-hero {
    float: right;
  }

}

@media only screen and (min-width: 401px) and (max-width: 960px) {
  #athena-hero {
    display: none;
  }

  #about-h .athena-primary {
