  }
}

@media screen and (max-width: 600px) {
  #about-h {
    margin-top: 15vh;
  }

  #athena-hero {
    margin-top: 0;
    display: none;
  }

  #about-h .athena-primary {
    margin-bottom: 5vh;
  }

}

#pathways-row {
  padding-top: 2vh;
  padding-bottom: 5vh;
  padding-left: 16vw;
  padding-right: 16vw;
  width: 100%;
  height: auto;
}

#pathways-header {
  text-align: center;
  padding-top: 5vh;
}

#pathways-row img {
  padding: 25px;
  height: 240px;
}

#pathways-row .athena-primary {
  font-size: 0.8em;
}

#path-cards p {
  font-size: 0.9em;
  text-align: left;
}

#path-cards h3 {
  font-weight: bold;
}

#path-cards {
  padding-top: 5vh;
}

#path-card, #path-card-r {
  height: auto;
  width: 20vw;
  border: 1px solid #F3EAFD;
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-bottom: 5px;
}

#path-card-r {
  margin-right: 4vw;
}

#underlined:after{
  content: url(/static/media/underline-association.924864f3.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -120px;
}
#underlined-companies:after{
  content: url(/static/media/underline-companies.70e4b48e.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -105px;
}
#underlined-schools:after{
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -75px;
}

@media only screen and (min-width: 401px) and (max-width: 1024px) {

  #pathways-row img {
    padding: 10%;
  }

  #path-card-r {
    margin-right: 0;
  }

  #path-card, #path-card-r {
    height: 35%;
    width: 33%;
  }

}

@media screen and (max-width: 600px) {
  #path-card-r {
    margin-right: 0;
    margin-bottom: 1vh;
  }

  #path-card, #path-card-r {
    height: 20%;
    width: 100%;
    display: block;
  }

  #pathways-row img {
    display: none;
  }
}

#reason-header {
  text-align: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
}

#reason-row {
