  text-align: center;
}

@media only screen and (min-width: 401px) and (max-width: 1024px) {

  #p3 {
    margin-left: 40%;
  }

  #p4 {
    float: right;
    margin-left: 0;
  }

  #stats-img-3 p:after {
    content: url(/static/media/underline-support.ff91cc2f.svg);
    position: absolute;
    margin-top: 14px;
    margin-left: -66px;
  }

}

@media screen and (max-width: 600px) {
  #stats-img-3 p:after, #stats-img-4 p:after {
    position: relative;
    margin-top: 0;
    margin-left: 0;
  }

  #img-3, #img-4 {
    float: none;
  }

  #p3, #p4 {
    margin-left: 0;
  }

  #top-row {
    display: block;
    height: auto;
    padding-left: 10%;
  }

  #top-row div {
    width: 50%;
  }

  #stats-img-3 p:after {
    content: url(/static/media/underline-support.ff91cc2f.svg);
    position: absolute;
    margin-top: 14px;
    margin-left: -66px;
  }

}

#footer-row {
