}

#school-header h1:after {
  content: url(/static/media/header-underline.78f57dc7.svg);
  position: absolute;
  margin-left: -170px;
  margin-top: 15px;
}

#about-header h1:after {
  content: url(/static/media/header-underline.78f57dc7.svg);
  position: absolute;
