}

.athena-small-widget {
  height: auto;
  width: 16vw;
  margin-bottom: 8vh;
  margin-right: 2vw;
  border: none;
  -webkit-filter: drop-shadow(2px 2px 0 rgba(0, 0, 0, 0.1));
          filter: drop-shadow(2px 2px 0 rgba(0, 0, 0, 0.1));
}

#separator {
  margin-top: -5vh;
  width: 13vw;
}

#yellow-separator {
  width: 13vw;
}

#desc {
  height: 20%;
}

.athena-small-widget h3 {
  font-size: 0.9em;
  font-weight: bold;
  text-align: center;
}

.athena-small-widget p {
  font-size: 0.7em;
}

.athena-small-widget .athena-primary {
  font-size: 0.8em;
  font-weight: bold;
  margin-bottom: 10%;
  margin-left: 25%;
}

.tag-list {
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 10%;
  width: 100%;
  overflow-x: scroll;
}

.tag-list::-webkit-scrollbar {
  display: none;
}

.badge {
  background-color: #F3EAFD;
  color: rgb(0,0,0,0.54);
  border-radius: 0;
  margin-right: 0.5vw;
  font-weight: normal;
  font-family: "Work Sans";
}

#athena-load {
  margin-top: 5vh;
  margin-left: 30vw;
  margin-bottom: 15vh;
}

#loading {
  margin-top: 5vh;
  margin-left: 46vw;
  border-radius: 50% !important;
}

.athena-modal-header, .athena-modal-body, .athena-modal-footer {
  padding-left: 8vw;
  padding-right: 8vw;
}

.athena-modal-header {
  background-color: #F9F9F9;
  border: 1px solid #CCCCCC;
}

