  font-weight: bold;
  color: rgb(0,0,0,0.8);
  margin-top: 5vh;
}

.athena-modal-body p {
  color: #505051;
}

.athena-modal-footer .athena-primary {
  margin-right: 10vw;
}

.review {
  font-style: italic;
}

@media screen and (max-width: 600px) {
