    padding-top: 5vh;
    padding-left: 5vw;
    height: 20vh;
    text-align: center;
  }

  #loading {
    margin-left: 35vw;
  }

  .athena-small-widget h3 {
    font-size: 1.2em;
    height: 5vh;
  }

  .athena-small-widget p {
    height: 2vh;
  }

  .athena-small-widget {
    width: 60vw;
    margin-bottom: 1vh;
    left: 10%;
  }
}

#about-row {
  background-color: white;
}

#what-img {
  padding-top: 100px;
  margin-left: 15vw;
}

#what-separator, #why-separator, #how-separator {
  width: 100%;
}

#what {
  width: 50%;
  height: 35vh;
  background-color: white;
  border: 2px solid #F9F9F9;
  padding: 25px;
