  padding: 30px;
  margin: 10px 0 10px 0;
}

#how {
  width: 50%;
  height: auto;
  background-color: white;
  border: 2px solid #F9F9F9;
  padding: 30px;
  margin: 10px 0 0 0;
}

#how-img {
  padding-top: 100px;
  margin-right: 15vw;
}

@media screen and (max-width: 1024px) {
  #what-img {
    display: none;
  }

  #what {
    width: 100%;
    height: auto;
  }

  #how {
    width: 100%;
    height: auto;
  }

  #why {
    height: auto;
  }

  #how-img {
    display: none;
  }


}

.sign-up, .sign-in {
  margin: 5vh 0 5vh 0;
